import { useCallback, useState } from 'react';
import Box from '../Components/Box';
import CodePanel from '../Components/CodePanel';
import Tabs from '../Components/Tabs';
import useFetch from '../Hooks/useFetch';
import { useDebounce } from 'use-debounce';

export default function FlexBox() {

  const
  [styles, setStyles] = useState<string[]>([]),
  [debounce] = useDebounce(styles, 1000),

  {data} = useFetch('/Snippets/FlexBox.css'),

  handleInput = (id: number) => {
    return (style: string) => {
      const update = [...styles];
      update[id] = style;
      setStyles(update);
    };
  };

  return (
    <Tabs tabs={['row', 'column']}>
      <>
        <style scoped>{debounce[1]}</style>
        <CodePanel
        // eslint-disable-next-line
        onInput={useCallback(handleInput(1), [])}
        lang={{CSS: data}} />
        <div className="panel" id="container1">
          <Box id={1} styles={{ lineHeight: '50px' }} />
          <Box id={2} />
          <Box id={3} />
          <Box id={4} />
          <Box id={5} />
          <Box id={6} />
        </div>
      </>
      <>
      </>
    </Tabs>
  );
};