import Code from './CodePanel/Code';
import Tabs from './Tabs';
import './CodePanel/Style.scss';
import { CodeProps } from './CodePanel/CodeProps';
import useHighlight from '../Hooks/useHighlight';

export default function CodePanel({ lang, indent = true, onInput = () => {} }: CodeProps) {

  useHighlight();

  return (
    <div className="panel language-markup">
      <Tabs tabs={Object.keys(lang)}>
        {Object.entries(lang).map(
          (entry, key) => <Code indent={indent} key={key} lang={{[entry[0]]: entry[1]}} onInput={onInput} />
        )}
      </Tabs>
    </div>
  );
};