
import { useEffect } from 'react';
import removeIndentation from './removeIndentation';
import useToast from '../../Hooks/useToast';
import { CodeProps } from './CodeProps';

export default function Code({ indent = true, lang, onInput = () => {} }: CodeProps) {
  
  const toast = useToast();
  let code = Object.values(lang)[0];

  if (!indent) {
    code = removeIndentation(code)
  }

  useEffect(() => onInput(code), [onInput, code]);

  return (
    <>
      <button
      className="reset"
      onClick={e => {
        const text = e.currentTarget.parentNode!.querySelector('code')!.textContent!;
        navigator.clipboard.writeText(text)
        .then(() => toast('copied to clipboard'))
        .catch(() => toast('copy to clipboard failed'));       
      }}>
        Copy to Clipboard
      </button>
      <pre>
        <code
        contentEditable
        className={"hljs language-" + Object.keys(lang)[0].toLowerCase()}
        spellCheck="false"
        suppressContentEditableWarning={true}
        onInput={e => {
          const el = e.target as HTMLElement;
          onInput(el.textContent as string);
        }}>
          {code}
        </code>
      </pre>
    </>
  );
};